@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
body {
  min-height: 100vh;
  background-color: #ffdada;
  background-image: url(https://img.itch.zone/aW1nLzE2MTM4NDEucG5n/original/%2B9g6CM.png);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "poppins";
  margin: 0;
  padding: 0;
}

h1 {
  font-family: "Press Start 2P", cursive;
  color: #2c3233;
  text-align: center;
  margin-bottom: 15px;
  margin-top: 25px;
}

a {
  color: #2c3233;
}

a:hover {
  color: #ffdada;
}

input[type="email"],
input[type="password"],
input[type="text"],
input[type="file"] {
  color: #2c3233;
  border-color: #ffdada;
}

input[type="email"]:focus,
input[type="password"]:focus,
input[type="text"]:focus,
input[type="file"]:focus {
  color: #2c3233;
  border-color: #ffdada;
  outline: 2px solid #2c3233;
}

.card {
  background-color: white;
  color: #2c3233;
  border-color: #2c3233;
}

.form-control {
  background-color: white;
}

.form-btn {
  margin-top: 35px;
  margin-bottom: 15px;
  padding: 8px 15px;
  display: flex;
  justify-content: flex-end;
  background-color: white;
  color: #2c3233;
  border: 1px solid #2c3233;
  border-radius: 20px;
  position: relative;
}

.form-btn:hover {
  color: white;
  background-color: #ffdada;
  border-color: #ffdada;
}

.heading-1 {
  text-align: center;
  font-family: "Press Start 2P", cursive;
  font-weight: 600;
  font-size: 2rem;
}

.heading-2 {
  color: #2c3233;
  font-family: "Press Start 2P", cursive;
  margin-top: 15px;
  margin-bottom: 25px;
  font-weight: 500;
}

.heading-3 {
  font-family: "Press Start 2P", cursive;
  color: #2c3233;
}

.container-tictactoe {
  display: flex;
}

/* style button */
.invite-btn {
  margin: 0;
  background-color: white;
  color: #2c3233;
  border: 1px solid #2c3233;
  border-radius: 20px;
}

.invite-btn:hover,
.leave-btn:hover {
  color: white;
  background-color: #2c3233;
  border-color: #2c3233;
}

.play-btn {
  margin-top: 25px;
  display: flex;
  width: 300px;
  justify-content: space-around;
}

.leave-btn {
  margin: 0;
  color: white;
  border: 1px solid white;
  border-radius: 20px;
}

/* sidebar online player */
.sidebar {
  margin-left: 5%;
  margin-right: 10%;
  width: 20%;
  text-align: left;
}

.sidebar-content {
  max-height: calc(100vh - 4rem - 87px);
  overflow-y: auto;
}

.online-player {
  color: #31326f;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

/* board game tic tac toe */
/* .board {
  height: calc(100vh - 4rem - 15px);
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
} */

.board {
  margin-top: 20px;
  width: 325px;
}

#message {
  margin-top: 20px;
  color: #31326f;
  font-family: "poppins";
  font-weight: 600;
  font-size: 20;
}

.board button {
  height: 95px;
  width: 95px;
  float: left;
  margin: 5px;
  font-size: 3em;
  background-color: rgba(255, 255, 255, 0.3);
  border: 5px solid #31326f;
  border-radius: 25px;
  transition: background 0.2s ease-in-out;
}
.board button:hover {
  cursor: pointer;
  background: white;
}

.Kotak {
  display: grid;
  width: 300px;
  height: 300px;
  grid-template-columns: auto auto auto;
}
.block {
  width: 95px;
  height: 95px;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  font-weight: bold;
  border: 5px solid #dbf6e9;
  border-radius: 25px;
  transition: background 0.2s ease-in-out;
  box-shadow: 8px 8px 30px 0px rgba(0, 0, 0, 0.15);
}

.block:hover {
  cursor: pointer;
  background: #31326f;
}

/* profile */
.profile {
  margin-top: 20px;
  margin-right: 35px;
  top: 0;
  right: 0;
  position: fixed;
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.profile-pic {
  -webkit-clip-path: circle();
          clip-path: circle();
  width: 100px;
}

.link {
  margin-right: 20px;
}

.link:hover {
  color: #f2ecff;
}

@media only screen and (max-width: 929px) {
  .container-tictactoe {
    display: flex;
    flex-direction: column;
  }
  .sidebar {
    order: 2;
    width: 60%;
  }
  .board-container {
    margin: auto;
    order: 1;
  }
  .profile {
    position: relative;
    margin: auto;
  }
}

.container-profile {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-profile {
  width: 40%;
  margin-bottom: 20px;
}

.stat {
  display: flex;
  justify-content: space-between;
}

.back-btn {
  margin-right: 20px;
  background-color: #2c3233;
  color: white;
}

.edit-btn:hover {
  background-color: white;
  color: #2c3233;
  border: 1px solid #2c3233;
}

@media screen and (max-width: 1024px) {
  .card-profile {
    width: 60%;
  }
}

@media screen and (max-width: 768px) {
  .card-profile {
    width: 90%;
  }
}

